/*section-title*/
.section-title {
  max-width: 720px;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 60px;
}
.section-title .sub-title {
  display: block;
  margin-bottom: 12px;
  color: var(--mainColor);
  text-transform: uppercase;
  font-size: 15.5px;
  font-weight: 700;
}
.section-title h2 {
  margin-bottom: 0;
  font-size: 42px;
}
.section-title p {
  max-width: 600px;
  font-size: 17px;
  font-weight: 600;
  margin-left: auto;
  margin-right: auto;
  margin-top: 12px;
}
.section-title .learn-more-btn {
  margin-top: 20px;
}

/*default-btn*/
.default-btn {
  text-align: center;
  display: inline-block;
  transition: var(--transition);
  border-radius: 5px;
  border: none;
  padding: 10px 30px;
  position: relative;
  z-index: 1;
  color: var(--whiteColor);
  background-color: #00adee;
  font-size: 17px;
  font-weight: 700;
}
.default-btn::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  border-radius: 5px;
  background-color: var(--mainColor);
  z-index: -1;
  transition: var(--transition);
}
.default-btn:hover {
  color: var(--whiteColor);
}
.default-btn:hover::before {
  transform: scaleX(0);
}

/*form-control*/
.form-control {
  height: 50px;
  color: #0d486c;
  box-shadow: unset !important;
  border: 1px solid #f5f5f5;
  background-color: #f5f5f5;
  transition: var(--transition);
  border-radius: 3px;
  font-size: 16px;
  font-weight: 600;
}
.form-control::placeholder {
  color: #7e7e7e;
  transition: var(--transition);
}
.form-control:focus {
  border-color: var(--mainColor);
  background-color: transparent;
}
.form-control:focus::placeholder {
  color: transparent;
}

textarea.form-control {
  height: auto;
  padding-top: 15px;
}

/*learn-more-btn*/
.learn-more-btn {
  text-align: center;
  display: inline-block;
  color: #C0A26C;
  transition: var(--transition);
  border-radius: 5px;
  padding: 9px 25px 9px 55px;
  position: relative;
  font-size: 18px;
  font-weight: 700;
}
.learn-more-btn .left-icon {
  display: inline-block;
  width: 45px;
  height: 45px;
  position: absolute;
  left: 0;
  top: 0;
  line-height: 46px;
  background-color: #C0A26C;
  color: var(--whiteColor);
  border-radius: 5px;
  transition: var(--transition);
  font-size: 30px;
  font-weight: normal;
}
.learn-more-btn .right-icon {
  position: absolute;
  right: 20px;
  top: 2px;
  transition: var(--transition);
  opacity: 0;
  visibility: hidden;
  font-size: 28px;
  font-weight: normal;
}
.learn-more-btn:hover {
  background-color: #C0A26C;
  color: var(--whiteColor);
  padding-left: 25px;
  padding-right: 55px;
}
.learn-more-btn:hover .left-icon {
  opacity: 0;
  visibility: hidden;
}
.learn-more-btn:hover .right-icon {
  color: var(--whiteColor);
  opacity: 1;
  visibility: visible;
}

